.btn {
  padding:1vh;
  text-decoration: none;
  color: rgb(255, 255, 255);
  -webkit-transition: all 0.3s;
  transition: all 0.3s;
  position: relative;
  margin: 2vh;
}


.btn::before {
  content: ' ';
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 1;
  opacity: 0;
  -webkit-transition: all 0.3s;
  transition: all 0.3s;
  border-top-width: 3px;
  border-bottom-width: 3px;
  border-top-style: solid;
  border-bottom-style: solid;
  border-top-color: rgba(255,255,255);
  border-bottom-color: rgba(255,255,255);
  -webkit-transform: scale(0.3, 1.1);
          transform: scale(0.3, 1.1);
}

.btn:hover::before {
  opacity: 1;	
  -webkit-transform: scale(1, 1);	
          transform: scale(1, 1);	
  
}
.btn::after {
  content: '';
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 1;
  -webkit-transition: all 0.3s;
  transition: all 0.3s;
  background-color: transparent;

}
.btn:hover::after {
  opacity: 0;	
  -webkit-transform: scale(0.1, 1);	
          transform: scale(0.1, 1);
  
}


