}
.navLink:hover::before {
  opacity: 1;	
  -webkit-transform: scale(1, 1);	
          transform: scale(1, 1);	
  
}
.navLink::after {
  content: '';
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 1;
  -webkit-transition: all 0.3s;
  transition: all 0.3s;
